ne;\n        background: none;\n        border: none;\n        font-size: 24px;\n        cursor: pointer;\n        position: absolute;\n        top: -25px;\n        right: 15px;\n    }\n\n    .overlay {\n        position: fixed;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        background: rgb(0 0 0 / 50%);\n        z-index: 999;\n    }\n\n    @media (width <= 768px) {\n        margin: 0;\n    }\n\n    @media (width <= 768px) {\n        .menu-button {\n            display: block;\n        }\n    }\n}\n\n.content {\n    display: flex;\n    justify-content: space-between;\n    max-width: 960px;\n    padding: 31px 83px;\n\n    @media (width <= 768px) {\n        flex-direction: column;\n        padding: 20px;\n    }\n}\n\n.main-content {\n    margin-left: 40px;\n\n    @media (width <= 768px) {\n        margin: 0\n    }\n}\n\n\n","@import '@styles/variables';\n\n.filter {\n    width: 232px;\n    border-radius: 5px;\n    background-color: $white-background;\n    box-shadow: 0 2px 8px 0 #0000001A;\n    position: relative;\n    height: max-content;\n\n    .filter-content {\n        display: flex;\n        flex-direction: column;\n        position: relative;\n        \n\n        .close-button {\n            display: none;\n            background: none;\n            border: none;\n            position: absolute;\n            top: -20px;\n            right: 0;\n        }\n\n        \n        &.open {\n            display: block;\n        }\n        \n        h2 {\n            color: $black-font;\n            font-weight: 600;\n            font-size: 13px;\n            text-transform: uppercase;\n            padding: 14px 20px 18px;\n\n            @media (width <= 768px) {\n                padding-top: 20px;\n            }\n        }\n        \n        .checkbox-group {\n            display: flex;\n            flex-direction: column;\n            \n            .checkbox-item {\n                :hover {\n                    background-color