eft-radius: 5px;\n            border-right: none;\n        }\n\n        &:last-child {\n            border-top-right-radius: 5px;\n            border-bottom-right-radius: 5px;\n            border-left: none;\n        }\n\n        @media (width <= 768px) {\n            padding: 10px 20px;\n            font-size: 9px;\n        }\n    }\n\n    @media (width <= 768px) {\n        display: block;\n        width: max-content;\n    }\n}\n","@import '@styles/variables';\n\n.flight {\n    display: flex;\n    flex-direction: column;\n    background-color: $white-background;\n    border-radius: 5px;\n    padding: 20px;\n    margin-bottom: 22px;\n    box-shadow: 0 2px 8px 0 #0000001A;\n\n    .flight-detail-group {\n        display: flex;\n        justify-content: space-between;\n        margin-bottom: 10px;\n\n        .airport-logo {\n            width: 130px;\n\n            @media (width <= 768px) {\n                width: 108px;\n            }\n        }\n\n        .flight-total-price {\n            color: $blue-font;\n            font-size: 24px;\n            font-weight: 600;\n            padding-top: 14px;\n\n            @media (width <= 768px) {\n                font-size: 16px;\n            }\n        }\n\n\n        .detail-item{\n            width: 33%;\n        }\n    }\n\n    @media (width <= 768px) {\n        padding: 12px;\n    }\n}\n","@import '@styles/variables';\n\n.show-more {\n    display: block;\n    width: 100%;\n    padding: 10px 0;\n    background-color: $blue-background;\n    color: $white-font;\n    border: none;\n    border-radius: 5px;\n    cursor: pointer;\n    text-align: center;\n    text-transform: uppercase;\n    font-size: 12px;\n    font-weight: 600;\n    letter-spacing: 0.5px;\n    margin-top: 20px;\n    outline: none;\n}\n","@import '@components/App/App';\n@import '@components/Filter/Filter';\n@import '@components/Tabs/Tabs';\n@import '@components/Flight/Flight';\n@import '@components/ShowMoreButton/ShowMoreButton';\n@import 'variables';\n\nbody